body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.left-section {
  flex: 1;
  padding: 75px;
  text-align: center;
}

.left-section img {
  max-width: 177%;
  height: 679px;
}

.transparent-box {
  position: absolute; 
  top: 59%; 
  left: 2%; 
  width: 42%;
  height: 40%; 
  background-color: rgba(0, 0, 0, 0.5); 
  color: white; 
  text-align: center; 
  display: flex; 
  justify-content: center;
  align-items: center; 
}


.text-inside-box {
  font-size: 18px;
  padding: 56px;
}
.right-section {
  flex: 1;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  max-width: 442px;
  margin: 50px auto;
  height: 642px;
  padding: 20px;
  border: 1px solid #ccc;
}

.right-section  {
  color: #333333;
  margin-bottom: 53px;
  text-align: center;
}

h2 {
  color: #5576c8;
  margin-bottom: 20px;
  text-align: center;
}
.para {
  color: #5576c8;
  margin-bottom: 20px;
  text-align: center;
}
button.sign {
  background-color: white;
  color: #007bff;
  border: 1px solid #007bff;
}


button.sign:hover {
  background-color: #007bff;
  color: #ffffff;
}


.form-group {
  margin-bottom: 15px;
  position: relative;
}

.input-group {
  display: flex;
  margin-bottom: 15px;
  justify-content: space-between; 
}

.input-group input {
  flex: 1;
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #cccccc;
  border-radius: 4px;
}

.input-group input:last-child {
  margin-right: -18px; 
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #cccccc;
  border-radius: 4px;
}

button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

.error,
.success {
  text-align: center;
  margin-top: 10px;
}

.error {
  color: red;
}

.success {
  color: rgb(36, 169, 9);
}

@keyframes fadeOut {
  to {
    opacity: 0;
    visibility: hidden;
  }
}